import React, { Component } from "react";
import {
    Grid,
    Row,
    Col,
} from "react-bootstrap";

import { Card } from "components/Card/Card.jsx";
import { FormInputs } from "components/FormInputs/FormInputs.jsx";
import { UserCard } from "components/UserCard/UserCard.jsx";
import Button from "components/CustomButton/CustomButton.jsx";
import Axios from "axios"


const base_url = 'http://127.0.0.1:8000/';

function getDescription () {
    return document.getElementById("exampleFormControlTextarea1").value;
}

function getName () {
    return document.getElementById("name").value;
}

function addProjectToDb() {

    fetch(base_url + 'social/current_profile/', {
        method : 'GET',
        headers : {
            Authorization : `JWT ${localStorage.getItem('token')}`
        }
    })
        .then(res => res.json())
        .then(resp => {

            console.log(getName())
            Axios.post(base_url + 'social/projects/create', {
                'project' : {
                    'created_by' : resp["user"],
                    'name' : getName(),
                    'description' : getDescription(),
                }})
                .then(response => {
                    console.log(response.config.data)
                    console.log(response.status + " " + response.statusText)
                })
                .catch(error => {
                    console.log(error)
                })
        })
        .catch(err => console.log(err));
}

function emptyFields() {
    document.getElementById("exampleFormControlTextarea1").value = null;
    document.getElementById("name").value = null;
}

function presentApp() {

    let path = '/home/projects';
    window.location.href=path
}

function waitTime() {

    setTimeout(function(){
        addProjectToDb();
    }, 1000);
    setTimeout(function(){
        presentApp();
    }, 10000);

}

class CreateProject extends Component{

    render(){
        return (
            <div className="content">
                <Grid fluid>
                    <Row>
                        <Col md={8}>
                            <Card
                                title="Add Project"
                                content={
                                    <form>
                                        <FormInputs
                                            ncols={["col-md-6"]}
                                            properties={[
                                                {
                                                    label: "Project Name",
                                                    id: "name",
                                                    type: "text",
                                                    bsClass: "form-control",
                                                    placeholder: "Name",
                                                },
                                            ]}
                                        />
                                        <div className="form-group">
                                            <label htmlFor="exampleFormControlTextarea1">
                                                Project Description
                                            </label>
                                            <textarea
                                                className="form-control"
                                                id="exampleFormControlTextarea1"
                                                rows="5"
                                                placeholder="Enter your description here..."
                                            />
                                        </div>

                                        <Button onClick={() => waitTime()} bsStyle="info" pullRight fill>
                                            Create Project
                                        </Button>
                                        <div className="clearfix" />
                                    </form>
                                }
                            />
                        </Col>
                        <Col md={4}>
                            <UserCard
                                name="Mike Andrew"
                                userName="michael24"
                                description={
                                    <span>
                    "Project Manager/Normal User"
                  </span>
                                }
                                socials={
                                    <div>
                                        <Button simple>
                                            <i className="fa fa-github-square fa-lg" />
                                        </Button>
                                    </div>
                                }
                            />
                        </Col>
                    </Row>
                </Grid>
            </div>
        );

    }
}

export default CreateProject
