import React, { Component } from "react";
import {Grid, Row, Col, Table} from "react-bootstrap";
import { Card } from "components/Card/Card.jsx";
import { Tooltip, OverlayTrigger } from "react-bootstrap";
import { Tasks } from "components/Tasks/Tasks.jsx";
import Button from "components/CustomButton/CustomButton.jsx";
import { FormInputs } from "components/FormInputs/FormInputs.jsx";
import { thMember, tdMember } from "variables/Variables.jsx";
import {tasks_title} from "../components/Tasks/Tasks";
import Axios from "axios";

const Input = props => {
  return (
      <div>
        <input type="text" ref={props.inputRef} />
      </div>
  );
};

let text = [];
let currentText;

const createIssuePath = '/home/createissue';
const base_url = 'http://127.0.0.1:8000/';

let addTrigger = false;

const edit = <Tooltip id="edit_tooltip">Edit</Tooltip>;

function updateProject() {

  console.log(localStorage.getItem('currentProjectName'));
  console.log(localStorage.getItem('currentProjectDescription'));

  Axios.patch(base_url + 'social/projects/update/' + localStorage.getItem('currentProjectName') +'/', {'description': localStorage.getItem('currentProjectDescription')})
      .then(response => {
        console.log(response.data['description']);
      })
      .catch(error => {
        console.log(error.response)
      })
}

class ProjectView extends Component {

  addMember() {

    let email = document.getElementById("user_email").value;
    if(email === '') {
      alert("Please complete the field to add an user!")
      return;
    }
    let projectName = (window.location.href).slice((window.location.href).lastIndexOf('/') + 1);
    let finalProjectName = projectName.split('%20').join(' ');

    fetch(base_url + 'social/add_member/', {
      crossDomain : true,
      method : 'POST',
      headers : {
        Authorization : `JWT ${localStorage.getItem('token')}`,
        'Content-Type' : 'application/json',
      },
      body: JSON.stringify({name: finalProjectName, email: email}),
    })
        .then(res => res.json())
        .then(pk => {
          alert("Member added successfully!")
          document.getElementById("user_email").value = ''
        })
        .then(this.forceUpdateHandler = this.forceUpdateHandler.bind(this))
        .catch(error =>
        {
          alert("Please enter a valid email!")
          document.getElementById("user_email").value = ''
        })
  }

  async fetchIssues() {

    let projectName = (window.location.href).slice((window.location.href).lastIndexOf('/') + 1);
    let finalProjectName = projectName.split('%20').join(' ');

    await fetch(base_url + 'social/issues/', {
      crossDomain : true,
      method : 'POST',
      headers : {
        Authorization : `JWT ${localStorage.getItem('token')}`,
        'Content-Type' : 'application/json',
      },
      body: JSON.stringify({name: finalProjectName}),
    })
        .then(res => res.json())
        .then(result => {
          console.log("Issues: ", result)
          let i = 0;

          while(i < result.length)
          {
            console.log(result[i]);

            tasks_title.push(result[i]['title']);
            console.log(tasks_title);

            i++;
          }
        })
        .then(this.forceUpdateHandler = this.forceUpdateHandler.bind(this))
  }

  async fetchMembers() {

    let projectName = (window.location.href).slice((window.location.href).lastIndexOf('/') + 1);
    let finalProjectName = projectName.split('%20').join(' ');

    await fetch(base_url + 'social/project_members/', {
      crossDomain : true,
      method : 'POST',
      headers : {
        Authorization : `JWT ${localStorage.getItem('token')}`,
        'Content-Type' : 'application/json',
      },
      body: JSON.stringify({name: finalProjectName}),
    })
        .then(res => res.json())
        .then(result => {
          console.log("Members: ", result)
          let i = 0;
          while(i < result.length)
          {
            console.log(result[i]);
            tdMember.push([i, result[i]['username'], result[i]['email']]);
            i++;
          }

        })
        .then(this.forceUpdateHandler = this.forceUpdateHandler.bind(this))
  }

  constructor(props){
    super(props);
    this.fetchIssues().then(r => console.log("Fetched Issues"));
    this.fetchMembers().then(r => console.log("Fetched Members"));
  };

  forceUpdateHandler(){
    this.forceUpdate();
  };

  state = {
    value: '',
    savedValue: localStorage.getItem('currentProjectDescription')
  };

  addText = event =>
  {
    document.getElementById("textarea").disabled = false;
    document.getElementById("textarea").hidden = false;
    document.getElementById("savebutton").disabled = false;

    currentText = this.state.savedValue;
    addTrigger = true;

    this.setState({ value: this.state.textareaVal });
    this.state.textareaVal = "";

  };

  activateEdit = event =>
  {
    document.getElementById("textarea").disabled = false;
    document.getElementById("textarea").hidden = false;
    document.getElementById("savebutton").disabled = false;
    document.getElementById("addtext").disabled = true;

    this.state.textareaVal = this.state.savedValue;
    currentText = this.state.savedValue;
    this.state.savedValue = "";
    this.setState({ value: this.state.textareaVal });
  };

  saveText = event =>
  {
    text = this.state.textareaVal;

    this.setState({ savedValue: text });
    document.getElementById("textarea").disabled = true;
    document.getElementById("textarea").hidden = true;
    document.getElementById("savebutton").disabled = true;

    if(addTrigger)
    {
      console.log(this.state.savedValue);
      this.state.savedValue += this.state.textareaVal;
      console.log(this.state.textareaVal);
      this.setState({ savedValue: this.state.savedValue});
      addTrigger = false;
      localStorage.setItem('currentProjectDescription', currentText + this.state.textareaVal);
    }else
    {
      localStorage.setItem('currentProjectDescription', this.state.textareaVal);
    }

    document.getElementById("addtext").disabled = false;
    updateProject();
  };

  createLegend(json) {
    var legend = [];
    for (var i = 0; i < json["names"].length; i++) {
      var type = "fa fa-circle text-" + json["types"][i];
      legend.push(<i className={type} key={i} />);
      legend.push(" ");
      legend.push(json["names"][i]);
    }
    return legend;
  }

  componentDidMount() {
    document.getElementById("textarea").disabled = true;
    document.getElementById("textarea").hidden = true;
    document.getElementById("savebutton").disabled = true;
    document.getElementById("addtext").disabled = false;
  }

  render() {

    return (
        <div className="content">
          <Grid fluid>

            <Col md={7}>
              <Row md={8}>
                <Card rowsMax={20} onSubmit={this.submitText}
                      id="chartHours"
                      title="Description"
                      category="Project details"
                      content={
                        <div >
                          <tr>
                            <td>{this.state.savedValue}</td>
                            <OverlayTrigger placement="top" overlay={edit}>
                              <Button bsStyle="info" simple type="button" bsSize="md" onClick={this.activateEdit}>
                                <i className="fa fa-edit" />
                              </Button>
                            </OverlayTrigger>
                          </tr>
                          <textarea style={{width: '100%', height: '100%'}} id="textarea"
                                    value = {this.state.textareaVal}
                                    onChange={(event)=>{
                                      this.setState({
                                        textareaVal:event.target.value
                                      });
                                    }}
                          />
                          <Button style={{marginTop: 10, marginLeft:10, _height: 20, _weigh: 30, bsSizes:'large', fontSize: 13}} onClick={this.saveText} id="savebutton">
                            Save
                          </Button>
                          <Button style={{marginTop: 10, marginLeft:10, _height: 20, _weigh: 30, bsSizes:'large', fontSize: 13}} onClick={this.addText} id="addtext">
                            Add
                          </Button>
                        </div>
                      }
                />
              </Row>
              <Row md={6}>
                <Card
                    title="Issues"
                    category="Project issues"
                    content={
                      <div className="table-full-width">
                        <Button style={{marginTop: -100, marginLeft: 120}} onClick={() => {window.location.href = createIssuePath + "/"+ (window.location.href).slice((window.location.href).lastIndexOf('/') + 1)}}>
                          Add Issue
                        </Button>
                        <table className="table">
                          <Tasks />
                        </table>
                      </div>
                    }
                />
              </Row>

            </Col>

            <Col md={4} style={{marginLeft: 50}}>
              <Row md={4}>
                <Card
                    title="Members"
                    content={
                      <div>
                        <Table striped hover style={{marginTop: 2, marginLeft: -9}}>
                          <thead>
                          <tr>
                            {thMember.map((prop, key) => {
                              return <th key={key}>{prop}</th>;
                            })}
                          </tr>
                          </thead>
                          <tbody>
                          {tdMember.map((prop, key) => {
                            return (
                                <tr onClick={() => {console.log(prop[1])}}>
                                  {prop.map((prop, key) => {
                                    return <td key={key}>{prop}</td>;
                                  })}

                                </tr>
                            );
                          })}
                          </tbody>
                        </Table>
                        <Col md={8}>
                          <FormInputs
                              ncols={["col-lg-30"]}
                              properties={[
                                {
                                  type: "text",
                                  id: "user_email",
                                  bsClass: "form-control",
                                  placeholder: "User email",
                                },
                              ]}
                          />
                        </Col>
                        <Col md={1}>
                          <Button style={{marginRight:0, marginTop:21, _height: 20, _weigh: 50, bsSizes:'large', fontSize: 13, backgroundColor: 'white'}} onClick={() => this.addMember()}>
                            Add
                          </Button>
                        </Col>
                      </div>
                    }

                />
              </Row>
            </Col>

            <Row>
            </Row>
          </Grid>
        </div>
    );
  }
}

export default ProjectView;
